<template>
  <div class="shadowbox">
    <!-- 标题-->
    <div class="titleAndBtn">
      <h3 class="title">店铺管理</h3>
      <el-button class="saveBtn" size="small" @click="saveStore" type="primary">保存</el-button>
    </div>
    <!-- 管理项 -->
    <el-form ref="form" :model="shopData" label-width="80px">
      <el-form-item label="店铺名称">
        <el-input style=" width:350px" v-model="shopData.name" placeholder="请输入店铺名称"></el-input>
      </el-form-item>
      <el-form-item label="店铺公告">
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 4}"
          style=" width:350px"
          v-model="shopData.bulletin"
          placeholder="请输入店铺名称"
        ></el-input>
      </el-form-item>
      <el-form-item label="店铺头像">
        <el-upload
          id="box"
          class="avatar-uploader"
          :action="serverupload"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
        >
          <img v-if="shopData.avatar" :src="servershopimg+shopData.avatar" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="店铺图片">
        <el-upload
          :action="serverupload"
          list-type="picture-card"
          :file-list="fileList"
          :on-success="shopImgSuccess"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt />
        </el-dialog>
      </el-form-item>
      <el-form-item label="配送费">
        <el-input style=" width:350px" v-model="shopData.deliveryPrice" placeholder="请输入配送费"></el-input>
      </el-form-item>
      <el-form-item label="配送时间">
        <el-input style=" width:350px" v-model="shopData.deliveryTime" placeholder="请输入配送费"></el-input>
      </el-form-item>
      <el-form-item label="配送描述">
        <el-input style=" width:350px" v-model="shopData.description" placeholder="请输入配送费"></el-input>
      </el-form-item>
      <el-form-item label="店铺评分">
        <el-input style=" width:350px" v-model="shopData.score" placeholder="请输入配送费"></el-input>
      </el-form-item>
      <el-form-item label="销量">
        <el-input style=" width:350px" v-model="shopData.sellCount" placeholder="请输入配送费"></el-input>
      </el-form-item>
      <el-form-item label="活动">
        <el-checkbox-group v-model="supports">
          <el-checkbox v-for="(item,index) in allsupports" :key="index" :label="item"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="营业时间">
        <el-time-picker
          is-range
          v-model="shopData.date"
          range-separator="至"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
          placeholder="选择时间范围"
        ></el-time-picker>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
//API_EDTI_STORE
import {
  API_GET_STROE_INFO,
  SERVER_UPLOAD,
  SERVER_SHOP_IMG,
  API_EDTI_STORE
} from "@/API/apis.js";
export default {
  data() {
    return {
      id: "", //店铺id
      pics: [], //上传图片成功后，新图片数组
      supports: [], //当前选中的活动
      // 可选的活动
      allsupports: [
        "在线支付满28减5",
        "VC无限橙果汁全场8折",
        "单人精彩套餐",
        "特价饮品8折抢购",
        "单人特色套餐"
      ],
      shopData: {}, // 店铺信息
      imageUrl: "", //店铺头像
      // 店铺图片参数
      dialogImageUrl: "",
      dialogVisible: false,
      fileList: [], // 上传图片数组
      serverupload: SERVER_UPLOAD, //上传接口
      servershopimg: SERVER_SHOP_IMG, //店铺图片
      beforeinfo: {}, //修改前信息
      afterinfo: {}
    };
  },
  methods: {
    // 店铺头像上传成功后执行
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      this.shopData.avatar = res.imgUrl; //保存修改过后的图片地址
    },
    // 店铺图片方法
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 店铺图片上传成功
    shopImgSuccess(res) {
      console.log(res);
      this.pics.push(res.imgUrl);
    },
    // 点击保存店铺信息
    saveStore() {
      // 转换参数类型
      this.afterinfo = { ...this.shopData };
      this.afterinfo.supports = JSON.stringify(this.supports);
      this.afterinfo.date = JSON.stringify(this.shopData.date);
      this.afterinfo.pics = JSON.stringify(this.pics);
      // console.log(this.afterinfo, this.beforeinfo);

      // 如果修改了信息才发请求
      if (
        this.afterinfo.name != this.beforeinfo.name ||
        this.afterinfo.bulletin != this.beforeinfo.bulletin ||
        this.afterinfo.avatar != this.beforeinfo.avatar ||
        this.afterinfo.deliveryPrice != this.beforeinfo.deliveryPrice ||
        this.afterinfo.supports != this.beforeinfo.supports ||
        this.afterinfo.pics != this.beforeinfo.pics ||
        this.afterinfo.date != this.beforeinfo.date ||
        this.afterinfo.description != this.beforeinfo.description ||
        this.afterinfo.sellCount != this.beforeinfo.sellCount ||
        this.afterinfo.score != this.beforeinfo.score
      ) {
        API_EDTI_STORE(this.afterinfo).then(res => {
          if (res) {
            this.$message({
              message: "保存店铺信息成功",
              type: "success",
              duration: 1500
            });
            API_GET_STROE_INFO().then(res => {
              this.shopData = res.data.data;
              this.supports = this.shopData.supports;

              this.beforeinfo = { ...this.shopData };
              this.beforeinfo.supports = JSON.stringify(this.shopData.supports);
              this.beforeinfo.date = JSON.stringify(this.shopData.date);
              this.beforeinfo.pics = JSON.stringify(this.shopData.pics);
              this.fileList = res.data.data.pics.map(img => {
                return {
                  url: this.servershopimg + img
                };
              });
              // console.log(this.shopData);
            });
          }
        });
      } else {
        this.$message({
          message: "您未改变任何信息",
          type: "warning",
          duration: 1500
        });
      }
    }
  },
  created() {
    // 获取店铺信息
    API_GET_STROE_INFO().then(res => {
      this.shopData = res.data.data;
      this.supports = this.shopData.supports;

      this.beforeinfo = { ...this.shopData };
      this.beforeinfo.supports = JSON.stringify(this.shopData.supports);
      this.beforeinfo.date = JSON.stringify(this.shopData.date);
      this.beforeinfo.pics = JSON.stringify(this.shopData.pics);
      this.fileList = res.data.data.pics.map(img => {
        return {
          url: this.servershopimg + img
        };
      });
      // console.log(this.shopData);
    });
  }
};
</script>

<style lang="less" scoped>
.shadowbox {
  opacity: 0.8;
  border-radius: 5px;
  box-shadow: 0px 0px 4px 3px rgba(117, 113, 113, 0.281);
  width: 100%;
  padding-bottom: 30px;
  background-color: #fff;
  .titleAndBtn {
    border-bottom: 1px solid #edf0f6;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  .saveBtn {
    height: 50%;
    margin-top: 8px;
    margin-right: 5px;
  }
  .title {
    font-weight: normal;
    text-indent: 15px;
    margin-top: 0;
    line-height: 50px;
    margin-bottom: 0;
  }

  #box {
    height: 100px;
    width: 100px;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  #box:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .avatar {
    width: 100px;
    height: 100px;
    display: block;
  }
}
//}
</style>